<template>
  <el-card>
    <el-collapse>
      <el-collapse-item name="1">
        <template slot="title">
          <h3><i class="el-icon-view"></i> Thông tin chi tiết</h3>
        </template>
        <el-form class="chi-tiet" label-width="100px">
          <el-row :gutter="20">
            <el-col :span="24">
              <el-alert
                  :title="alert.title"
                  :type="alert.type"
                  :description="alert.content"
                  show-icon
                  center
                  :closable="false">
              </el-alert>
            </el-col>
          </el-row>
          <el-row>
            <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
              <el-form-item label="Ngày âm lịch: ">
                <span>{{ getLunar }}</span>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="40" v-if="alert.type !== 'success'">
            <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
              <el-form-item label="Chi tiết: ">
                <span>{{ toDay.content }}</span>
              </el-form-item>
            </el-col>
            <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
              <el-form-item label="Tác hại: ">
                <span>{{ toDay.harm || 'Không có' }}</span>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-collapse-item>
      <el-collapse-item name="2">
        <template slot="title">
          <h3 style="color: #42b983"><i class="el-icon-circle-check"></i> Ngày được phép trong tháng {{ getMonth }}</h3>
        </template>
        <el-popover v-for="(day, index) in dayAccept" :key="index"
                    placement="top-start"
                    title="Chi tiết"
                    width="200"
                    trigger="hover">
          <el-form>
            <el-form-item label="Âm lịch:">
              <span>{{ toDayInfo.date }}</span>
            </el-form-item>
          </el-form>
          <el-button slot="reference" type="success" class="tag" @mouseover.native="getDayInfo(day)">{{
              day | formatDateEE
            }}
          </el-button>
        </el-popover>
      </el-collapse-item>
      <el-collapse-item name="3">
        <template slot="title">
          <h3 style="color: goldenrod"><i class="el-icon-warning-outline"></i> Ngày kiêng trong tháng {{ getMonth }}
          </h3>
        </template>
        <el-popover v-for="(day, index) in daysWarning" :key="index"
                    placement="top-start"
                    title="Chi tiết"
                    trigger="hover">
          <el-form>
            <el-form-item label="Âm lịch:">
              <span>{{ toDayInfo.date }}</span>
            </el-form-item>
            <el-form-item label="Sự kiện:">
              <span>{{ toDayInfo.content }}</span>
            </el-form-item>
          </el-form>
          <el-button slot="reference" type="warning" class="tag" @mouseover.native="getDayInfo(day)">{{
              day | formatDateEE
            }}
          </el-button>
        </el-popover>
      </el-collapse-item>
      <el-collapse-item name="4">
        <template slot="title">
          <h3 style="color: crimson"><i class="el-icon-circle-close"></i> Ngày không được trong tháng {{ getMonth }}
          </h3>
        </template>
        <el-popover v-for="(day, index) in daysRefuse" :key="index"
                    placement="top-start"
                    title="Chi tiết"
                    trigger="hover">
          <el-form>
            <el-form-item label="Âm lịch:">
              <span>{{ toDayInfo.date }}</span>
            </el-form-item>
            <el-form-item label="Sự kiện:">
              <span>{{ toDayInfo.content }}</span>
            </el-form-item>
            <el-form-item label="Tác hại:">
              <span>{{ toDayInfo.harm }}</span>
            </el-form-item>
          </el-form>
          <el-button slot="reference" type="danger" class="tag" @mouseover.native="getDayInfo(day)">{{
              day | formatDateEE
            }}
          </el-button>
        </el-popover>
      </el-collapse-item>
    </el-collapse>
  </el-card>
</template>

<script>
import dateData from "@/dateData"
// import {formatDateEE} from '@/filters'
import moment from 'moment';
import 'moment-lunar';

const LIST_ALERT = [
  {title: 'Hôm nay là một ngày được phép', type: 'success', content: 'Được phép nhưng hãy điều độ'},
  {
    title: 'Hôm nay là một ngày kiêng nhưng không tác hại',
    type: 'warning',
    content: 'Không có tác hại nhưng cũng không nên để mất thành kính'
  },
  {title: 'Hôm nay là một ngày không được phép', type: 'error', content: 'Tuyệt đối không được'}
]

export default {
  name: "ChiTiet",
  data() {
    return {
      dateData: dateData,
      alert: {},
      toDay: {},
      toDayInfo: {},
      activeNames: []
    }
  },
  props: {
    chooseDate: {
      type: Date,
      default: function () {
        return new Date()
      }
    }
  },
  mounted() {
    this.checkDay(this.chooseDate)
  },
  watch: {
    chooseDate(val) {
      this.checkDay(val)
    }
  },
  computed: {
    getMonth() {
      return this.chooseDate.getMonth() + 1
    },
    getLunar() {
      return moment(this.chooseDate).lunar().format('DD-MM-YYYY')
    },
    daysRefuse() {
      return this.getDaysRefuseOrWarning(true)
    },
    daysWarning() {
      return this.getDaysRefuseOrWarning(false)
    },
    dayAccept() {
      return this.getDaysAccept()
    }
  },
  methods: {
    getDaysOfMonth() {
      let day = new Date(this.chooseDate.getFullYear() + '-' + this.getMonth + '-1')
      const days = []
      while (day.getMonth() + 1 === this.getMonth) {
        days.push(new Date(day.getTime()))
        day.setDate(day.getDate() + 1)
      }
      return days
    },
    getDayInfo(date) {
      const lunar = this.convertToLunar(date).format('DD-MM')
      let result = true
      this.dateData.every(s => {
        if (s.date === lunar) {
          this.toDayInfo = s
          result = false
          return false
        } else return true
      })
      if (result) {
        this.toDayInfo = {date: lunar}
      }
      console.log(this.toDayInfo)
    },
    checkDay(date) {
      const lunar = this.convertToLunar(date).format('DD-MM')
      let result = true
      this.dateData.every(s => {
        if (s.date === lunar) {
          this.toDay = s
          if (s.harm !== '') {
            this.alert = LIST_ALERT[2]
          } else this.alert = LIST_ALERT[1]
          result = false
          this.$message({
            message: this.alert.title,
            type: this.alert.type
          })
          return false
        } else return true
      })
      if (result) {
        this.alert = LIST_ALERT[0]
        this.$message({
          message: this.alert.title,
          type: this.alert.type
        })
      }
    },
    convertToLunar(date) {
      return moment(date).lunar()
    },
    convertToSolar(lunar) {
      return moment(lunar).solar()
    },
    getDaysRefuseOrWarning(refuse) {
      const days = this.getDaysOfMonth()
      return days.filter((date) => {
        let result = false
        this.dateData.every(s => {
          if (s.date === this.convertToLunar(date).format('DD-MM')) {
            if (refuse) {
              if (s.harm !== '') {
                result = true
                return false
              }
            } else {
              if (s.harm === '') {
                result = true
                return false
              }
            }
          } else return true
        })
        return result
      })
    },
    getDaysAccept() {
      const days = this.getDaysOfMonth()
      return days.filter((date) => {
        let result = true
        this.dateData.forEach(s => {
          if (s.date === this.convertToLunar(date).format('DD-MM')) {
            result = false
          }
        })
        return result
      })
    }
  }
}
</script>

<style scoped>
.tag {
  margin: 10px;
}
.chi-tiet > * {
  margin: 8px;
}
</style>
