import moment from 'moment'
moment.locale('vi')

const DD_MM_YYYY = 'DD-MM-YYYY'
const EEEE_DD_MM_YYYY = 'DD-MM-YYYY'
const DD_MM = 'dd-MM'

export function formatDate(date) {
    return moment(date).format(DD_MM_YYYY)
}

export function formatDateEE(date) {
    return moment(date).format(EEEE_DD_MM_YYYY)
}

export function formatDD_MM(date) {
    return moment(date).format(DD_MM)
}
