<template>
  <el-card>
    <el-form>
      <el-form-item label="Chọn ngày">
        <el-date-picker
            v-model="selectDate"
            @change="changeDate"
            format="dd-MM-yyyy"
            :clearable="false"></el-date-picker>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
export default {
  name: "ChonNgay",
  data() {
    return {
      selectDate: Date
    }
  },
  props: {
    chooseDate: {
      type: Date,
      required: true
    }
  },
  watch: {
    chooseDate: {
      immediate: true,
      handler: function (val) {
        this.selectDate = val
      }
    }
  },
  methods: {
    changeDate() {
      this.$emit('update:chooseDate', this.selectDate)
    }
  }
}
</script>

